import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import NewsletterBox from "../components/newsletterBox"
import SEO from "../components/seo"
import Heading from "react-bulma-components/lib/components/heading"
import Container from "react-bulma-components/lib/components/container"
import NewsletterSubscribe from "../components/newsletterSubscribe"
import Navbar from "../components/navbar"

export default ({ data }) => (
  <Layout>
    <SEO title="Aktuell" keywords={[`ecotainment`, `newsletter`, `aktuell`]} />
    <Container>
      <Navbar />
      <NewsletterSubscribe />
      <Heading size={3} renderAs="h2" style={{ padding: "25px" }}>
        Aktuelle Ausgabe von Ivo's Ecotainment Newsletter
      </Heading>
      {data.allNewJson.edges.map(({ node }, index) => (
        <NewsletterBox key={index} node={node} />
      ))}
    </Container>
  </Layout>
)

export const query = graphql`
  query {
    allNewJson(sort: { fields: published, order: DESC }, limit: 1) {
      edges {
        node {
          published
          title
          tags
          newsletter {
            welcome
            intro
            parts {
              emoji {
                label
                icon
              }
              title
              tags
              text
            }
            outro
          }
        }
      }
    }
  }
`
